import validationCheck from "./validationCheck.js";
import { TodoEach } from "./TodoEach.js";

export function TodoList({
  $target,
  initialState,
  handleComplete,
  handleDelete,
}) {
  if (!new.target) {
    throw new Error("Ïª¥Ìè¨ÎÑåÌä∏Î•º ÏÉùÏÑ±Ïûê Ìï®ÏàòÎ°ú Ìò∏Ï∂úÌï¥Ï£ºÏÑ∏Ïöî");
  }
  const $listWrap = document.createElement("div");
  $target.appendChild($listWrap);

  if (validationCheck(initialState)) {
    this.state = initialState;
  } else {
    throw new Error("ÏûÖÎ†•Í∞í ÌòïÏãùÏù¥ ÏûòÎ™ªÎêòÏóàÏäµÎãàÎã§");
  }

  this.setState = (nextState) => {
    this.state = nextState;
    this.render();
  };
  let isInit = false;

  this.render = () => {
    const rendering = this.render;
    if (!isInit) {
      $listWrap.innerHTML = "";
    }
    new TodoEach({
      $target: $listWrap,
      state: this.state,
      handleComplete,
      handleDelete,
      rendering,
    });
  };

  this.render();
}
